/* Navigation Bar */
.navbar {
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
  height: auto;
  flex-basis: auto;
  background: #1565c0;
  box-shadow: 0px 3px #4f98ec; }
  .navbar ul {
    list-style: none;
    text-align: right;
    width: 80%;
    margin: 0 auto;
    padding: 0.5em; }

.navbar-elem {
  display: inline-block;
  padding: 0.5em; }
  .navbar-elem a {
    position: relative;
    padding: 0.25em;
    color: #ffffff;
    font-size: 20px;
    text-decoration: none; }
  .navbar-elem a:after {
    content: "";
    position: absolute;
    right: 0;
    width: 0;
    bottom: -3px;
    background: #ffffff;
    height: 2px;
    -webkit-transition-property: width;
    transition-property: width;
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
            transition-timing-function: ease-out; }
  .navbar-elem a:hover:after,
  .navbar-elem a:focus:after,
  .navbar-elem a:active:after {
    left: 0;
    right: auto;
    width: 100%; }
  .navbar-elem .active:after {
    left: 0;
    right: auto;
    width: 100%; }

.app-body {
  width: 50%;
  margin: 1em auto 6em auto; }
  @media screen and (max-width: 1200px) {
    .app-body {
      width: 65%; } }
  @media screen and (max-width: 800px) {
    .app-body {
      width: 80%; } }
  @media screen and (max-width: 400px) {
    .app-body {
      width: 90%; } }
  .app-body h1 {
    font-size: 3.5em; }
  .app-body h1 {
    font-size: 2.5em; }
  .app-body .button-link {
    background: none !important;
    color: inherit;
    border: none;
    padding: 0 !important;
    font: inherit;
    cursor: pointer; }
  .app-body a, .app-body .button-link {
    color: #003c8f;
    text-decoration: none;
    font-weight: 600; }
  .app-body a:hover, .app-body .button-link:hover {
    color: #388be9; }

/* Home/Landing */
.landing {
