.publication-item em {
  font-weight: 600; }

.publication-item .publication-item-menu {
  list-style: none;
  -webkit-padding-start: 0;
          padding-inline-start: 0; }
  .publication-item .publication-item-menu li {
    display: inline-block;
    margin: 0 0.5em; }
  .publication-item .publication-item-menu li.bibtex-block {
