/** About **/
.about {
  display: flex; }
  @media screen and (max-width: 800px) {
    .about {
      flex-wrap: wrap; } }
  @media screen and (max-width: 400px) {
    .about {
      justify-content: center; } }
  .about .about-img-wrapper {
    margin: 0 1em 0 0;
    width: 300px;
    height: 300px; }
    .about .about-img-wrapper img {
      width: 300px;
      height: 300px;
      box-sizing: border-box;
      border: 0.5em solid #1565c0;
      border-radius: 50%; }
    .about .about-img-wrapper img:hover {
      -webkit-animation: 6s multicolor;
              animation: 6s multicolor;
      -webkit-animation-delay: 0.2s;
              animation-delay: 0.2s;
      -webkit-animation-iteration-count: infinite;
              animation-iteration-count: infinite; }
    @media screen and (max-width: 400px) {
      .about .about-img-wrapper {
        width: 100%;
        height: 100%; }
        .about .about-img-wrapper img {
          width: 100%;
          height: 100%; } }
  .about p {
    margin-left: 1em; }

@-webkit-keyframes multicolor {
  0% {
    border-color: #1565c0; }
  17% {
    border-color: #1abc9c; }
  34% {
    border-color: #f1c40f; }
  51% {
    border-color: #e67e22; }
  68% {
    border-color: #c0392b; }
  85% {
    border-color: #27ae60; }
  100% {
    border-color: #1565c0; } }

@keyframes multicolor {
  0% {
    border-color: #1565c0; }
  17% {
    border-color: #1abc9c; }
  34% {
    border-color: #f1c40f; }
  51% {
    border-color: #e67e22; }
  68% {
    border-color: #c0392b; }
  85% {
    border-color: #27ae60; }
  100% {
    border-color: #1565c0; } }

.activity-section h2 {
